@font-face {
  font-family: 'Inter';
  src: url('./Inter-Regular.eot');
  src: url('./Inter-Regular.eot?#iefix') format('embedded-opentype'),
    url('./Inter-Regular.woff2') format('woff2'),
    url('./Inter-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-stretch: normal;
  unicode-range: U+0020-E0D0;
  font-display: swap;
}

